<template>
  <div class="address-list">
    <address-header style="z-index: 999;background-color: #fff" line-style="background:#fff" title="我的收货地址" :goEdit="goEdit" :goEditAddress="goEditAddress" @historyBack="historyBack"></address-header>
    <scroll class="address-scroll" ref="addressScroll">
      <div>
        <div class="address-wrapper" ref="addressWrapper">
            <div v-for="item in addressList" :key="item.id" class="address-item" @click="checkAddress(item.id)">
              <div class="address-item-detailInfo">
                <div class="address-item-allAddress">
                  <div class="address-item-userInfo">
                    <span class="address-item-username">{{ item.realname }}</span>
                    <span class="address-item-phone">{{ item.mobile }}</span>
                  </div>
                  <span v-if="item.isDefault == 'Y' ? true : false" class="isDefault">默认</span>
                  {{item.province}} {{item.city}} {{item.district}} {{item.town}} {{item.address}}
                </div>
                <div class="address-item-edit" @click.stop="goEditAddress(item.id)">编辑</div>
              </div>
            </div>
        </div>
        <div v-if="addressList.length < 1">
          <div  class="no-address">
            <div class="no-address-content fadeIn">
              <img src="../address-list/images/no-address.png" alt="no address" class="">
              <p class="no-address-text">您还没有添加收货地址喔~</p>
            </div>
          </div>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script>
  import AddressHeader from '../../base/address-header/address-header.vue'
  import Scroll from '../../base/scroll/scroll'
  import * as core from '../../api/addressList'


  export default {
    name: 'address-list',
    data() {
      return {
        addressList: [],
        goEdit: true
      }
    },
    components: {
      AddressHeader,
      Scroll
    },
    created() {
      document.title = this.$route.meta.title
      this.getAddressList()
      this.saveParams()
    },
    methods: {
      getAddressList() {
        core.itemAddressList().then(res => {
          //console.log(res)
          if (res.code&&'00'===res.code){
            this.addressList = res.result;
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        })
      },
      goEditAddress(id) {
        this.$router.push({path:'/addressEdit',query:{addressId:id}})
      },
      saveParams(){
        //console.log(this.$route.query)
        if(!this.$route.query.pageName){
          window.localStorage.setItem("params",JSON.stringify(this.$route.query))
        }
      },
      checkAddress(id){
        if(window.localStorage.getItem("params") != "{}"){
          let data = JSON.parse(window.localStorage.getItem("params"))
          data.addressId = id
          this.$router.push({path:'/purchaseNow', query: data})
          window.localStorage.removeItem("params")
        }
      },
      historyBack(){
        if(window.localStorage.getItem("params") != "{}"){
          let data = JSON.parse(window.localStorage.getItem("params"))
          this.$router.push({path:'/purchaseNow', query: data})
          window.localStorage.removeItem("params")
        }else{
          window.history.back(-1)
        }
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/iconfont.styl"
  .address-list
    background-color rgb(245,245,245)
    height 100vh
    //padding-bottom: 0.625rem;
    overflow hidden

  .address-scroll
    height calc(100vh - 3.375rem)

  .address-wrapper
    padding-bottom: 0.625rem;

  .address-item
    margin-top 0.625rem
    padding 1rem
    background-color rgb(255,255,255)

    .address-item-userInfo
      font-size 0.9375rem
      margin-bottom 0.5rem
      .address-item-phone
        color rgb(153,153,153)
        margin-left 1.968rem

    .address-item-detailInfo
      display flex
      justify-content center
      font-size 0.8125rem
      align-items: center;

      .address-item-allAddress
        flex 1
        line-height 1.25rem
        padding-right 2px

        .isDefault
          display inline-block
          background-color rgb(255,242,222)
          color rgb(255,152,0)
          height 1.125rem
          line-height 1.125rem
          width 2.5rem
          text-align center

      .address-item-edit
        width 2.5625rem
        text-align right
        position relative
        color rgb(255, 72, 0)
        height 2rem
        line-height 2rem
        text-align right
        &::before
          content ''
          border 1px solid rgb(230,230,230)
          height 2rem
          position absolute
          left 0
          top 0rem

  .no-address
    position fixed
    top 0
    left 0
    right 0
    bottom 0
    .no-address-content
      position absolute
      top 6rem
      width 9.5rem
      left 50%
      transform translateX(-50%)
      text-align center
      img
        width 9.5rem
        height 6.78125rem
      .no-address-text
        margin 0.75rem 0 0.9375rem 0
        font-size 0.75rem
        color rgb(153,153,153)

.fadeIn {
    -webkit-animation: fadeIn .3s;
            animation: fadeIn .3s;
}

@-webkit-keyframes fadeIn {
    from {
      opacity: 0
    }
    to {
      opacity: 1
    }
}

@keyframes fadeIn {
    from {
        opacity: 0
    }
    to {
        opacity: 1
    }
}
</style>



